.placeholder {
  pointer-events: none;
  position: absolute;
  width: calc(100% + 5px);
  height: calc(100vh + 5px);
  z-index: 100;
  top: 0;
  left: 0;
  background: #fff;
  transform-origin: 0 0;
}
.placeholder.placeholder--trans-in {
  transition: transform 0.5s;
  transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
}
.placeholder.placeholder--trans-out {
  -webkit-transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
}
.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  pointer-events: none;
  height: 0;
/* Avoid second scroll bar in Chrome! */
  visibility: hidden;
  z-index: 400;
  overflow: hidden;
}
.content.content--show {
  height: 100vh;
  pointer-events: auto;
  visibility: visible;
}
.scroll-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.content__item {
  position: absolute;
  top: 0;
  width: 100%;
  overflow: hidden;
  height: 0;
  opacity: 0;
  padding: 5% 15%;
  pointer-events: none;
  text-align: center;
}
.content__item.content__item--show {
  height: auto;
  min-height: 100vh;
  opacity: 1;
  pointer-events: auto;
  transition: opacity 0.6s;
}
.close-button {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
}
.close-button span {
  display: none;
}
.close-button:hover {
  color: #7b7b7b;
}
.close-button--show {
  opacity: 1;
  pointer-events: auto;
}
h2 {
  text-align: center;
  font-size: 60px;
  margin-top: 0;
  font-weight: 100;
}
.main {
  position: relative;
  z-index: 50;
  width: 100%;
}
@media (min-width: 1024px) {
  .main {
    width: calc(100% - 300px);
    margin-left: 300px;
  }
}
.grid {
  position: relative;
  min-height: 100vh;
}
.grid::after {
  content: '';
  background: rgba(202,202,202,0.4);
  opacity: 0;
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.view-single .grid::after {
  opacity: 1;
}
.grid__item {
  padding: 55px 55px 30px;
  position: relative;
  color: inherit;
  background: #fff;
  min-height: 300px;
  height: 33.3333vh;
  cursor: pointer;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.grid__item::before {
  position: absolute;
  content: '';
  top: 0px;
  right: 55px;
  bottom: 0px;
  left: 55px;
  border-bottom: 1px solid rgba(74,74,74,0.075);
}
.grid__item * {
  z-index: 10;
}
.title {
  margin: 0;
  font-size: 1.875em;
  font-weight: 100;
  text-align: center;
}
.title--preview {
  -webkit-flex: 1;
  flex: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  width: 100%;
}
.grid__item .fa {
  font-size: 60px;
  margin-bottom: 0.5em;
}
.loader {
  height: 1px;
  width: 40%;
  margin: 1em auto;
  position: relative;
  background: rgba(255,255,255,0.1);
}
.loader::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  background: #a4e7ad;
  left: 0;
  top: -1px;
  transform: scale3d(0, 1, 1);
  transform-origin: 0% 50%;
}
.grid__item--loading .loader::before {
  transition: transform 1s;
  transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  transform: scale3d(1, 1, 1);
}
.grid__item > * {
  transition: transform 0.2s, opacity 0.2s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}
.grid__item .fa {
  transition-delay: 0.2s;
}
.title--preview {
  transition-delay: 0.15s;
}
.loader {
  transition-delay: 0.1s;
}
.grid__item--animate .title--preview {
  transition-delay: 0s;
  opacity: 0;
  transform: translate3d(0, -20px, 0);
}
.grid__item--animate .loader {
  transition-delay: 0.1s;
  opacity: 0;
  transform: translate3d(0, -30px, 0);
}
.grid__item--animate .fa {
  transition-delay: 0.15s;
  opacity: 0;
  transform: translate3d(0, -40px, 0);
}
@media (min-width: 600px) {
  .grid__item {
    width: 50%;
    float: left;
  }
}
.sidebar {
  text-align: center;
  background: #f5f5f5;
  z-index: 1000;
  position: fixed;
  width: 300px;
  max-width: 100%;
  height: 100vh;
  min-height: 320px;
  top: 0;
  left: -100%;
  padding: 2em 3em 5rem;
  transition: left 0.25s ease-in-out;
  color: #333;
}
.sidebar__address {
  padding: 5px;
  font-style: normal;
}
.sidebar__address span,
.sidebar__address a {
  display: block;
  text-align: left;
}
.sidebar__address span {
  color: #888;
}
.sidebar__address a {
  text-decoration: none;
  color: #333;
  margin-bottom: 1rem;
}
.sidebar__address a:hover {
  color: #0184cd;
}
.portrait {
  border: 1px solid #ddd;
  padding: 0.5rem;
  margin: 0 auto;
  display: block;
  width: 100%;
  max-width: 20rem;
}
.socials {
  list-style: none;
  padding-left: 0;
  font-size: 2rem;
  display: flex;
  justify-content: space-between;
}
.socials a {
  color: #888;
  padding: 1rem;
  transition: all 0.25s ease-in-out;
}
.socials .linkedin:hover {
  color: #1b85bd;
}
.socials .facebook:hover {
  color: #3b5998;
}
.socials .steam:hover {
  color: #171a21;
}
.sidebar__socials {
  position: absolute;
  bottom: 0;
}
@media (min-width: 1024px) {
  .sidebar {
    left: 0;
  }
}
body {
  width: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
}
body {
  margin: 0;
  background-size: cover;
  background-attachment: fixed;
  font-size: 15px;
  color: #fff;
  font-family: 'Roboto', sans-serif;
}
* {
  box-sizing: border-box;
}
figure {
  margin: 0;
}
.btn {
  padding: 15px;
}
/* helper class when showing content */
.noscroll {
  overflow: hidden;
}
